.accordian-main {
    cursor: pointer;
    display: inline-block;
    -webkit-text-decoration: none;
    text-decoration: none;
    font-size: 14px;
    background-color: #0a1acd;
    border: none;
    border-radius: 10px;
  
    padding: 10px;
    color: #fff;
  }
  
  .list-styles {
    list-style-type: circle;
  }
  
  .image-style1 {
    width: 3em;
  }
  
  .p-2{
    border-radius: 20%;
  }
.contact-jumbotron {
